
body, html, li, input, gloabl, h2, th, input, nav, td, h1, embed, blockquote, ol, xml, div, dl, dt, dd, ul, h3, h4, h5, h6, pre, form, fieldset, abbr, p, header, hgroup, Section, figure, section, nav, article, aside, canvas {
  margin: 0;
  padding: 0;
}
img {
  border: 0;
  padding: 0;
  margin: 0;
}
ol, ul, li {
  list-style: none;
}
input, textarea {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-appearance: none;
}

/* 自定义部分 */
body {
  font-family: sans-serif;
  font-size: 14px;
  background: white;
  color: black;
}

